import React from "react";
import { Container } from "reactstrap";
import parse from "html-react-parser";
import AnimatedElement from "../../shared-components/AnimatedElement";
import FileComponent from "../../shared-components/FileComponent";

const Aboutme = ({ aboutme }) => {
  return (
    <>
      {aboutme && (
        <Container className="text-center ">
          <AnimatedElement>
            <h1 className="p-color f-size-20 py-4 mt-5">
              {parse(`${aboutme.contentTitle}`)}
              <hr className="w-200px my-4 mx-auto" />
            </h1>
            <h3 className="t-color f-size-36 py-3">
              {/* Creative Frontend Developer based in India */}
              {parse(`${aboutme.contentHeading}`)}
            </h3>
            <div className="t-color f-size-20 pb-5">
              {parse(`${aboutme.contentData}`)}
            </div>
          </AnimatedElement>
          <div className="py-3 mb-5">
            <FileComponent
              fileSrc={aboutme.attachment}
              alt={aboutme.contentHeading}
              fileClass={
                ["jpg", "jpeg", "png", "gif"].includes(
                  aboutme.attachment.split(".").splice(-1)[0]
                )
                  ? "rounded-0 px-5 text-decoration-none"
                  : "sp-btn-secondary rounded-0 px-5 text-decoration-none"
              }
            />
          </div>
        </Container>
      )}
    </>
  );
};

export default Aboutme;
