import React, { useEffect, useState } from "react";
import { Button, Container } from "reactstrap";
import parse from "html-react-parser";
import { getBase64ImageReducer } from "../../reducers/commonReducer";
import { useDispatch } from "react-redux";

const HomeBanner = ({ banner, userProfile }) => {
  const [bannerBase64Image, setBannerBase64Image] = useState("");
  const dispatch = useDispatch();
  const getBase64Image = async (banner) => {
    const fileName = banner.bannerImage;
    if (fileName !== "" && fileName !== null) {
      const data = await dispatch(getBase64ImageReducer({ fileName }));
      if (data.payload && data.payload.fileBase64) {
        setBannerBase64Image(
          `data:image/jpeg;base64,${data.payload.fileBase64}`
        );
      } else {
        setBannerBase64Image(`/uploads/${banner.bannerImage}`);
      }
    }
  };

  useEffect(() => {
    getBase64Image(banner);
  }, []);
  return (
    <>
      <Container fluid className="m-0 p-0">
        <div
          className="homebanner-image"
          style={{ backgroundImage: `url(${bannerBase64Image})` }}
        >
          <div
            className="d-md-flex justify-content-md-between position-relative homebanner-content"
            style={{ height: "100%" }}
          >
            <div className="banner-left mx-auto pt-5 d-flex flex-column justify-content-around">
              {/* <div className="h-175px d-sm-block d-md-none"></div> */}
              <h2 className="text-start f-size-42 pt-5">
                {userProfile.name}
                <span className="name-dot">.</span>
                <hr className="w-25 ms-5" />
              </h2>
              <p className="text-start py-md-4">
                Email Id: {userProfile.email}
                <br />
                Contact No: {userProfile.mobile}
                {userProfile.mobile2 ? " / " + userProfile.mobile2 : ""}
                <br />
                Skype ID: {userProfile.skypeId}
              </p>
              <Button className="sp-btn-secondary w-200px rounded-0">
                CONTACT ME
              </Button>
            </div>
            <div className="banner-right mx-auto mt-md-5 pt-5 d-none d-md-flex flex-column justify-content-around position-absolute">
              <div className="text-center f-size-20 py-2">
                Introduction
                <hr className="w-50 m-auto" />
              </div>

              <h2>
                <span className="f-size-50 p-4 d-block text-center">
                  {parse(`${banner.introductionL1}`)}
                </span>
                <span className="f-size-36 mt-2 d-block text-center">
                  {parse(`${banner.introductionL2.split(" ").join("&nbsp;")}`, {
                    trim: false,
                  })}
                </span>
              </h2>
            </div>
          </div>
        </div>
        <div className="banner-right mx-auto mt-md-5 pt-5 d-flex flex-column justify-content-around d-md-none">
          <div className="text-center f-size-20 py-2">
            Introduction
            <hr className="w-50 m-auto" />
          </div>

          <h2>
            <span className="f-size-50 p-4 d-block text-center">
              {parse(`${banner.introductionL1}`)}
            </span>
            <span className="f-size-36 mt-2 d-block text-center">
              {parse(`${banner.introductionL2.split(" ").join("&nbsp;")}`, {
                trim: false,
              })}
            </span>
          </h2>
        </div>
      </Container>
    </>
  );
};

export default HomeBanner;
