import React from "react";
import { Container } from "reactstrap";
import AnimatedElement from "../../shared-components/AnimatedElement";
import FileComponent from "../../shared-components/FileComponent";

const HomeMiddleBanner = (props) => {
  return (
    <>
      <Container fluid className="m-0 p-0">
        <div className="home-middle-banner position-relative my-5">
          <AnimatedElement>
            <FileComponent
              fileSrc={props.middleBanner.bannerImage}
              alt="I am delivering beautiful digital products for my clients"
              fileClass={"w-100"}
            ></FileComponent>
          </AnimatedElement>
        </div>
      </Container>
    </>
  );
};

export default HomeMiddleBanner;
